import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Spinner, Avatar, ModalRoot, ModalPage, ModalPageHeader, Tabs, TabsItem, List, Cell} from '@vkontakte/vkui';
import Axios from 'axios';
import '@vkontakte/vkui/dist/vkui.css';
import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';

const api = new VKMiniAppAPI();

class Modal extends React.Component {
    constructor(props) {
        super(props)
          this.state = {
              activeModal: 'rating',
              activeTab:'all',

          }
    }

      componentDidMount() {
        api.initApp();

      }


      render() {
          return(
              <ModalRoot activeModal={this.props.activeModal}>
                <ModalPage id="rating" onClose={this.props.back} header={<ModalPageHeader>Рейтинг</ModalPageHeader>} dynamicContentHeight>

                    <Tabs>
                      <TabsItem selected={this.state.activeTab == 'all'} onClick={()=>this.setState({activeTab: 'all'})}>Общий</TabsItem>
                      <TabsItem selected={this.state.activeTab == 'friends'} onClick={()=>this.setState({activeTab: 'friends'})}>Друзья</TabsItem>
                    </Tabs>


                    {this.state.activeTab === 'all' ? 
                     <div>
                       {this.props.rating_all != null ? 
                     <List>
                       {this.props.rating_all.map((data,i)=>
                         <Cell key={i} before={<Avatar src={data.photo_200} />} indicator={data.current_ballans} asideContent={"+"+data.k+" /c"}>{data.name}</Cell>
                       )}
                        
                     </List>:
                     <Spinner size="large" /> }
                     </div>  
                     :
                     <div />
      }
                </ModalPage>
              </ModalRoot>
          )
      }
}

export default Modal