import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import {View, Panel, Group, PanelHeader, Spinner, Header, Card, CardGrid, Avatar, Snackbar, ConfigProvider} from '@vkontakte/vkui';
import Axios from 'axios';
import '@vkontakte/vkui/dist/vkui.css';

import Modal from './panels/Modal';
import Icon48Camera from '@vkontakte/icons/dist/48/camera';
import Icon56LinkCircleOutline from '@vkontakte/icons/dist/56/link_circle_outline';
import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';
import Icon28CancelCircleOutline from '@vkontakte/icons/dist/28/cancel_circle_outline';
import Icon28DoneOutline from '@vkontakte/icons/dist/28/done_outline';

const api = new VKMiniAppAPI();

class App extends React.Component {
 constructor(props) {
   super(props)
   this.state = {
	  user:null,
	  userInfo:null,
	  activeModal:null,
	  rating_all:null,
	  snackbar:null,
	  scheme: "bright_light",
	  o:null
   }
 }

    componentDidMount() {
		api.initApp();
		api.onUpdateConfig(this.updateConfig())
		Axios.post('https://bot-api-vk.000webhostapp.com/api.place_quests/',JSON.stringify({type:"log_in",obj:{url:window.location.href}})).then(data=>{
			console.log(data)
			 this.setState({user:data.data});
            
		   })
		   setInterval(()=>{
			 Axios.post("https://bot-api-vk.000webhostapp.com/api.place_quests/", JSON.stringify({type:'tick', hash:this.state.user.hash}))
			 .then(data=>{this.setState({user:{
				 ...this.state.user,
				 current_ballans:data.data
			 }})})
		   }, 999)
	}

	 updateConfig = (o) => {

		this.setState({scheme: o.scheme, o:o})
		 
	 }

	go_rating = () => {
		Axios.post("https://bot-api-vk.000webhostapp.com/api.place_quests/", JSON.stringify({type:"get_rating"})).then(data=>{
			console.log(data);
			this.setState({rating_all: data.data})
		  })
		}

	openQRreader = () => {
		api.openCodeReader()
		.then(data=>{
			Axios.post("https://bot-api-vk.000webhostapp.com/api.place_quests/", JSON.stringify({type:"set_k", hash:this.state.user.hash, key:data}))
			.then(data=>{
               if(data.data == 'not') {
                 this.setState({snackbar: <Snackbar before={<Icon28CancelCircleOutline fill="#FB2626"/>} onClose={()=>this.setState({snackbar: null})}>Ошибочка</Snackbar>})
			   }else {
				this.setState({snackbar: <Snackbar before={<Icon28DoneOutline fill="#00CE40"/>} onClose={()=>this.setState({snackbar: null})}>Ok</Snackbar>, user:{
					...this.state.user,
					k:Number(this.state.user.k)+Number(data.data)
				}})
			   }
			})
		})
	}	
	

	render() {
		return(
	<ConfigProvider isWebView={true} scheme={this.state.scheme}>		
		<View activePanel="home" modal={<Modal activeModal={this.state.activeModal} back={()=>this.setState({activeModal:null})} rating_all={this.state.rating_all} />}>
          <Panel id="home">
           <PanelHeader>Home</PanelHeader>
            {this.state.user == null ? <Spinner size="large"/> :
			  <Group align="center" header={<Header mode='secondary' style={{textAlign: 'center'}} aside={"+"+this.state.user.k}>Ваш счет</Header>}>
			     <span style={{fontSize:"350%", fontWeight:"bold"}}>{this.state.user.current_ballans}</span>
				 <CardGrid style={{paddingTop:20}}>
				   <Card size='l' style={{backgroundImage: 'linear-gradient(135deg, #f24973 0%, #3948e6 100%)'}} onClick={this.openQRreader}>
					   <div style={{height:85}} align='center'>
						   <Icon48Camera fill="#FFFFFF"style={{marginTop:12}}/>  
						   <span color='white' style={{color: 'white'}}>Сканировать</span>
					   </div>
				   </Card>
				   <Card size='l'>
				        <div style={{height:85}} align='center' onClick={()=>{this.setState({activeModal:'rating'}); this.go_rating()}}>
							<Icon56LinkCircleOutline />
							<span>Рейтинг</span>
						</div>
				   </Card>
				 </CardGrid>
				 {this.state.o}
			  </Group>
			}
			{this.state.snackbar}
		  </Panel>
		</View>
	</ConfigProvider>	
		)
	}

}
export default App;

